<template>
    <div class="hotGoods-wrap">
        <div class="title">
            <img src="../../../assets/goods_p.png">
            <span>今日秒抢</span>
        </div>
        <div class="sold">
            <div class="sold_time">
                <ul class="clearfix" :style="{width:stime.length*1.5+'rem'}">
                    <li class="fl" v-for="(item,index) in stime" :key="item.id" :class="{active:status[index]}" @click="tab(item,index)">{{item.name}}</li>
                </ul>
            </div>
        </div>
        <ul class="content-main clearfix">
            <li class="fl" v-for="(item,index) in data" :key="item.id" @click="tolink(index)">
                <div class="image">
                    <img v-if="item.pic_url" v-lazy="URL + item.pic_url">
                </div>
                <p class="desi">{{item.title}}</p>
                <p class="price">￥<span>{{item.price_market}}</span></p>
            </li>
        </ul>
    </div>    
</template>
<script>
    export default {
        name : 'hotGoods',
        data() {
            return {
                stime:'',
                status:[true,false]
            }
        },
        props:{
            data:null
        },
        created(){
             this.axios({
                url:API_URL+'Home/Index/getSoldTime',
                method:'post'
            }).then((res) => {
            console.log(res);
               this.stime = res.data.data;
            }).catch((err) => {
                console.log(err)
            });
        },

        methods:{
            tab(item,index){
                for(var i = 0; i < this.status.length; i++){
                    this.$set(this.status,i,false);
                }
                this.$set(this.status,index,true);
            },
            tolink(index){
                this.$router.push({
                    name:'product',
                    params:{
                        id:this.data[index].id,
                        status:1
                    }
                });
            }
        }
    }
</script>
<style lang="less" scoped>
    .hotGoods-wrap{
        background:#f2f2f2;
        .title {
            background:#fff;
            height:.8rem;
            text-align:center;
            span {
                font-size:.3rem;
                color:#363636;
                line-height:1rem;
            }
            img {
                vertical-align: middle;
                display: inline-block;
                width:.45rem;
                height:.45rem;
                margin-bottom:.15rem;
            }
        }
        .sold {
            height:1rem;
            overflow:hidden;
            .sold_time {
                background:#fff;
                height:1.6rem;
                overflow-x:auto;
                ul {
                    height:1.6rem;
                    li {
                        text-align:center;
                        width:1.45rem;
                        height:1rem;
                        line-height:1rem;
                        padding:0 .15rem;
                        font-size:.3rem;
                        color:#727171;
                        font-weight:500;
                    }
                    li.active{
                       color:#ed3851;
                    }
                }
            }
        }
        
        .content-main{
            li{
                width:3.7rem;
                height:4.96rem;
                background:#fff;
                margin-top:.1rem;
                margin-right:.08rem;
                .image{
                    width:3.7rem;
                    height:3.7rem;
                    img {
                    width:100%;
                    height:100%;
                    }
                }
                .desi{
                    font-size:.3rem;
                    color:#333;
                    padding:.2rem .2rem;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    border-top:1px solid #f2f2f2;
                    box-sizing: border-box;
                }
                .price{
                    padding-left:.2rem;
                    color:#e02828;
                    font-size:.3rem;
                    span{
                        font-size:.36rem;
                    }
                }
            }
            li:nth-child(2n){
                margin-right:0;
            }
        }
    }
</style>


