<template>
  <div class="goods">
    <header class="cart-header">
      我的购物车({{$store.state.cart_data.length}})
      <!-- <router-link to="/myNews"><span></span></router-link> -->
    </header>
    <div class="cart_dl">
      爱微购
      <span class="clearfix" @click="deit">
        <i class="fl">{{total.fn}}</i>
        <em class="fl"></em>
      </span>
    </div>
    <div id="mescroll" class="mescroll">
      <dl class="mark-wrap" v-if="$store.state.cart_data">
          
        <div v-for="(data,index) in sendAddress" :key="data.id">
          <p class="depot-address">{{data.stock_name}}</p>
          <dd
            class="clearfix"
            v-if="data.id == item.send_address"
            v-for="(item,index) in $store.state.cart_data"
            :key="item.id"
            :class="{active:!total.deit}"
          >
            <label class="checkBox" :class="{active:status[index]}" @click="addCom(index,item)">
              <span></span>
            </label>
            <img v-lazy="URL + item.pic_url" class="fl" @click="enterDetail(index,item)" />
            <div class="pull-right fl" v-show="total.deit">
              <p class="text" @click="enterDetail(index,item)">{{item.title}}</p>
              <p class="price-wrap clearfix">
                <span class="fl price" @click="enterDetail(index,item)">
                  ￥
                  <span>{{item.price_member}}</span>
                </span>
                <span
                  class="prompt"
                  v-if="item.statime > nowTime || item.statime <endTime"
                >不在秒抢时段，不能购买</span>
                <span class="number fr">x{{item.goods_num}}</span>
              </p>
            </div>
            <div class="delete fr" v-show="!total.deit" @click="remove(index,item)">
              <div class="icon"></div>
              <div class="text">删除</div>
            </div>
            <div class="input-wrap fr" v-show="!total.deit">
              <button @click="reduce(index,item)">-</button>
              <input type="number" v-model="item.goods_num" />
              <button @click="add(index,item)">+</button>
            </div>
          </dd>
        </div>
      </dl>
      <div class="cart-active text-center" v-if="!$store.state.cart_data">
        <img src="../../assets/cart.png" />
        <p class="text">您的购物车没有任何商品，快去逛逛吧！</p>
      </div>
    </div>

    <!-- <div class="interested" v-show="total.deit" v-if="$store.state.cart_data">
                            <div class="title">
                                <span></span>
                                <span></span>
                                您可能感兴趣的产品
                            </div>
                            <hot-goods :data="$store.state.computer"></hot-goods>
    </div>-->
    <div class="footer-wrap" v-if="$store.state.cart_data">
      <div class="footer clearfix">
        <label class="chicked fl" :class="{active:this.total.seat}" @click="seat">
          <span></span>
          {{total.setData}}
        </label>
        <button class="set-btn fr" v-show="total.deit" @click="toOrder">去结算({{total.seleNumber}})</button>
        <div class="total fl" v-show="total.deit">
          <div class="total-metre clearfix">
            <span class="title fl">总计&nbsp;:&nbsp;</span>
            <span class="con fl">
              ￥
              <span>{{total.price|keepTwoNum}}</span>
            </span>
          </div>
          <div
            class="total-price"
          >总额：￥{{total.price|keepTwoNum}}&nbsp;&nbsp;优惠：￥{{total.dis|keepTwoNum}}</div>
        </div>
        <!-- <div class="immig fr" v-show="!total.deit" @click="collection">移入收藏</div>
        <div class="share fr" v-show="!total.deit">分享</div>-->
      </div>
    </div>
    <div class="load" v-show="load" @touchmove.prevent>
      <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
    </div>
    <div class="load-wrap" v-show="$store.state.cart_load" @touchmove.prevent>
      <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
    </div>
  </div>
</template>

<script>
import { MessageBox, Toast } from "mint-ui";
import qs from "qs";
import hotGoods from "@/components/home/children/hotGoods";
import Shopsn from "@/components/page/Shopsn.vue";
export default {
  name: "cart",
  data() {
    return {
      load: false,
      load_wrap: true,
      goods_id_list: [],
      total: {
        seleNumber: 0,
        newPrice: 0,
        price: 0,
        dis: 0,
        seat: "",
        deit: true,
        setData: "全选",
        fn: "编辑"
      },
      scrollWatch: true,
      data: "",
      status: [],
      computer: "",
      goos_id: [],
      goods: [],
      sendAddress: [],
      nowTime: 0,
      endTime: 0,
      cart_goods: []
    };
  },
  beforeRouteLeave(to, from, next) {
    this.$store.state.changeHomeScrollY = 0;
    this.$store.state.groupScrollY = 0;
    this.$store.state.groupData = [];
    next();
  },
  methods: {
      
    addCom(index, item) {

      //单选
      if (this.status[index] == true) {
        this.$set(this.status, index, false);
        this.$set(this.goods_id_list, index, "");
        this.total.seleNumber--;
        this.total.price -= item.price_member * item.goods_num;
        this.goos_id.splice(index, 1);
        this.goods.splice(index, 1);
      } else {
        // if (item.statime > this.nowTime || item.statime < this.endTime) {
        //   return;
        // }
        this.$set(this.status, index, true);
        this.$set(this.goods_id_list, index, item.id);
        this.total.seleNumber++;
        this.total.price += item.price_member * item.goods_num;
        this.$set(this.goos_id, index, item.goods_id);
        this.$set(this.goods, index, {
          num: item.goods_num,
          id: item.goods_id,
          goods_price: item.price_member,
          send_address: item.send_address
        });
      }
      if (this.total.seleNumber >= this.$store.state.cart_data.length) {
        this.total.seat = true;
      } else {
        this.total.seat = false;
      }
    },
    seat() {
      //全选
      this.total.price = 0;
      this.goods_id_list = [];
      if (this.total.seat == true) {
        this.total.seat = false;
        for (let i = 0; i < this.$store.state.cart_data.length; i++) {
          this.$set(this.status, i, false);
          this.goods_id_list.splice(i, 1);
        }
        this.goos_id = [];
        this.goods = [];
        this.total.seleNumber = 0;
        this.total.price = 0;
      } else {
        let seleNumbe = 0;
        this.total.seat = true;
        for (let i = 0; i < this.$store.state.cart_data.length; i++) {
          if (
            this.$store.state.cart_data[i].statime > this.nowTime ||
            this.$store.state.cart_data[i].statime < this.endTime
          ) {
            continue;
          }
          seleNumbe++;
          this.$set(this.goods_id_list, i, this.$store.state.cart_data[i].id);
          this.$set(this.status, i, true);
          this.total.price +=
            this.$store.state.cart_data[i].price_member *
            this.$store.state.cart_data[i].goods_num;
          this.$set(this.goos_id, i, this.$store.state.cart_data[i].goods_id);
          this.$set(this.goods, i, {
            num: this.$store.state.cart_data[i].goods_num,
            id: this.$store.state.cart_data[i].goods_id,
            goods_price: this.$store.state.cart_data[i].price_member,
            send_address: this.$store.state.cart_data[i].send_address
          });
        }
        this.total.seleNumber = seleNumbe;
      }
    },
    enterDetail(index, item) {
      this.$router.push({
        name: "product",
        params: {
          id: item.goods_id,
          status: 1
        }
      });
    },
    deit() {
      //编辑购物车
      if (this.total.deit == true) {
        this.total.fn = "完成";
        this.total.deit = false;
      } else {
        this.total.fn = "编辑";
        this.total.price = 0;
        this.goods = [];
        this.goos_id = [];
        for (var i = 0; i < this.goods_id_list.length; i++) {
          for (
            var index = 0;
            index < this.$store.state.cart_data.length;
            index++
          ) {
            if (
              this.goods_id_list[i] == this.$store.state.cart_data[index].id
            ) {
              this.$set(
                this.goos_id,
                index,
                this.$store.state.cart_data[index].goods_id
              );
              this.total.price +=
                this.$store.state.cart_data[index].price_member *
                this.$store.state.cart_data[index].goods_num;
              this.$set(this.goods, index, {
                num: this.$store.state.cart_data[index].goods_num,
                id: this.$store.state.cart_data[index].goods_id,
                goods_price: this.$store.state.cart_data[index].price_member,
                send_address: this.$store.state.cart_data[index].send_address
              });
            }
          }
        }
        this.total.deit = true;
      }
    },
    reduce(index, item) {
      //商品减少
      if (this.$store.state.cart_data[index].goods_num <= 1) return;
      this.$store.state.cart_data[index].goods_num--;
      this.subtract(2, item.id);
    },
    add(index, item) {
      //商品增加
      this.$store.state.cart_data[index].goods_num++;
      this.subtract(1, item.id);
    },
    subtract(n, _id) {
      this.load = true;
      this.axios
        .post(
          API_URL + "Home/Cart/addReduce",
          qs.stringify({
            app_user_id: sessionStorage.getItem("user_ID"),
            type: n,
            id: _id
          })
        )
        .then(res => {
          this.load = false;
        })
        .catch(err => {
          console.log(err);
        });
    },
    remove(index, item) {
      //从购物车中删除商品
      MessageBox.confirm("确定删除此商品?")
        .then(action => {
          this.axios
            .post(
              API_URL + "Home/Cart/delete",
              qs.stringify({
                app_user_id: sessionStorage.getItem("user_ID"),
                id: item.id
              })
            )
            .then(res => {
              this.$store.state.cart_data.splice(index, 1);
              if (this.$store.state.cart_data.length <= 0) {
                this.$store.state.cart_data = "";
              }
              Toast("删除成功！");
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    toOrder() {
      //去结算
      this.load = true;
      for (var i = 0, len = this.goods.length; i < len; i++) {
        //重塑数组
        if (
          !this.goods[i] ||
          this.goods[i] == null ||
          this.goods[i] == "" ||
          this.goods[i] == undefined
        ) {
          this.goods.splice(i, 1);
          len--;
          i--;
        }
      }
      var ss = 0;
      for (var i = 0, leng = this.goos_id.length; i < leng; i++) {
        if (this.goos_id[i]) {
          ss++;
        }
      }
      if (this.goos_id == "" || ss == 0) {
        Toast("请选择您要购买的物品！");
        this.load = false;
        return;
      }
      this.$store.state.goods = JSON.stringify(this.goods);
      this.$router.push({
        name: "order",
        params: {
          id: 1
        }
      });
    },
    collection() {
      //添加到收藏
      for (var i = 0, len = this.goos_id.length; i < len; i++) {
        //重塑数组
        if (
          !this.goos_id[i] ||
          this.goos_id[i] == null ||
          this.goos_id[i] == "" ||
          this.goos_id[i] == undefined
        ) {
          this.goos_id.splice(i, 1);
          len--;
          i--;
        }
      }
      if (this.goos_id == "") {
        Toast("请选择您要收藏的物品！");
        return;
      }
      this.axios
        .post(
          API_URL + "Home/Cart/muchCollection",
          qs.stringify({
            app_user_id: sessionStorage.getItem("user_ID"),
            goods_id: JSON.stringify(this.goos_id)
          })
        )
        .then(res => {
          Toast(res.data.msg);
        })
        .catch(err => {
          console.log(err);
        });
    },
    downCallback() {
      this.status = [];
      this.goos_id = [];
      this.goods = [];
      this.goods_id_list = [];
      this.total = {
        seleNumber: 0,
        newPrice: 0,
        price: 0,
        dis: 0,
        seat: "",
        deit: true,
        setData: "全选",
        fn: "编辑"
      };
      this.getCartData(1);
    },
    getCartData(s) {
      this.axios
        .post(
          API_URL + "Home/Cart/myCart",
          qs.stringify({
            app_user_id: sessionStorage.getItem("user_ID")
          })
        )
        .then(res => {
          this.$store.state.cart_load = false;
          if (res.data.status == 1) {
            if (s === 1) {
              setTimeout(() => {
                this.mescroll.endSuccess(); //无参
              }, 200);
            }
            this.$store.state.cart_data = res.data.data.goods;
            this.cart_goods = this.$store.state.cart_data;
            this.sendAddress = res.data.data.sendAddress;
            this.nowTime = res.data.data.now;
            this.endTime = res.data.data.end;
          } else {
            this.$store.state.cart_data = "";
            if (s === 1) {
              this.mescroll.endSuccess(); //无参
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  watch: {
    cart_goods() {
      if (this.cart_goods.length === 0) return;
      let addressId = [];
      for (let index = 0; index < this.cart_goods.length; index++) {
        addressId.push(this.cart_goods[index].send_address);
      }
      for (let index = 0; index < this.sendAddress.length; index++) {
        if (!addressId.includes(this.sendAddress[index].id)) {
          this.sendAddress.splice(index, 1);
        }
      }
    }
  },
  components: {
    hotGoods,
    Shopsn
  },
  mounted() {
    //创建MeScroll对象
    var self = this;
    self.mescroll = new MeScroll("mescroll", {
      //下拉刷新
      down: {
        callback: self.downCallback, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
        use: self.down, //是否初始化下拉刷新; 默认true
        auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
        autoShowLoading: false, //如果在初始化完毕之后自动执行下拉回调,是否显示下拉刷新进度; 默认false
        isLock: false, //是否锁定下拉,默认false;
        isBoth: false, //下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认false,两者不可同时触发;
        offset: 60, //触发刷新的距离,默认80
        outOffsetRate: 0.2, //超过指定距离范围外时,改变下拉区域高度比例;值小于1且越接近0,越往下拉高度变化越小;
        bottomOffset: 20, //当手指touchmove位置在距离body底部20px范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
        minAngle: 45, //向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
        hardwareClass: "mescroll-hardware", //硬件加速样式;解决iOS下拉因隐藏进度条而闪屏的问题,参见mescroll.css
        warpId: null, //可配置下拉刷新的布局添加到指定id的div;默认不配置,默认添加到mescrollId
        warpClass: "mescroll-downwarp", //容器,装载布局内容,参见mescroll.css
        resetClass: "mescroll-downwarp-reset" //高度重置的动画,参见mescroll.css
      }
    });
    document.body.scrollTop = 0;
    // this.axios.post(API_URL+'Home/Cart/cart_recommend',qs.stringify({
    //     app_user_id:sessionStorage.getItem('user_ID')
    // })).then((res) => {
    //     this.$store.state.computer = res.data.data;
    // }).catch((err) => {
    //     console.log(err);
    // });
    if (sessionStorage.getItem("user_ID")) {
      this.getCartData();
    } else {
      this.$router.push("/LogoIn");
    }
  },
  destroyed() {
    this.scrollWatch = false;
  }
};
</script>

