        #d0d0d0,
        #d0d0d0 50%,
        transparent 50%
      );
      background-size: 120% 1px;
      background-repeat: no-repeat;
      background-position: bottom left;
      background-origin: content-box;
    }
    dd.active {
      background: #fafafa;
    }
    dd {
      padding: 0.25rem 0;
      padding-left: 0.7rem;
      height: 1.55rem;
      position: relative;
      img {
        width: 1.55rem;
        height: 1.55rem;
      }
      .delete {
        width: 1.2rem;
        height: 100%;
        background: #f9781e;
        position: absolute;
        right: 0;
        top: 0;
        color: #fff;
        text-align: center;
        .icon {
          width: 0.51rem;
          height: 0.51rem;
          background: url(../../assets/delete.png) no-repeat;
          background-size: 100% 100%;
          margin: 0.55rem auto 0.2rem;
        }
        .text {
          font-size: 0.24rem;
        }
      }
      .input-wrap {
        margin-right: 1.2rem;
        input[type="number"] {
          border: none;
          width: 1.8rem;
          text-align: center;
          font-size: 0.36rem;
          color: #333;
          background: #fafafa;
          outline: none;
        }
        button {
          border: none;
          background: none;
          color: #999;
          outline: none;
          width: 0.8rem;
          text-align: center;
          font-size: 0.5rem;
        }
      }
      .pull-right {
        padding-left: 0.2rem;
        .text {
          width: 4.6rem;
          height: 0.65rem;
          font-size: 0.24rem;
          color: #333;
          line-height: 0.32rem;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .price-wrap {
          padding-top: 0.4rem;
          .price {
            font-size: 0.26rem;
            color: #e02828;
            span {
              font-size: 0.38rem;
            }
          }
          .prompt {
            font-size: 0.2rem;
            padding-left: 0.2rem;
            color: #ed3851;
          }
          .number {
            font-size: 0.32rem;
            color: #333;
          }
        }
      }
      .checkBox.active {
        color: #ce0a14;
        span {
          width: 0.44rem;
          height: 0.44rem;
          border: none;
          background: url(../../assets/check_bj.png) no-repeat;
          background-size: 100% 100%;
        }
      }
      .checkBox {
        position: absolute;
        left: 0;
        top: 0;
        width: 0.7rem;
        height: 100%;
        span {
          position: absolute;
          top: 50%;
          left: 0;
          margin-top: -0.2rem;
          width: 0.4rem;
          height: 0.4rem;
          border: 1px solid #8d8d8d;
          border-radius: 50%;
          text-align: center;
          line-height: 0.4rem;
          font-size: 0.3rem;
          color: #f9781e;
        }
      }
    }
  }
}
</style>


